import { Component, Input, Output, EventEmitter, HostListener } from '@angular/core';
import { Pagination, AlertService, gettext, ActionControl, Row } from '@c8y/ngx-components';
import { InventoryService } from '@c8y/client';
import { SubAssetsService } from '../sub-assets.service';
import { IManagedObject } from '@c8y/client';
import { QueriesUtil } from '@c8y/client';
import { AssignDevicesService } from './assign-devices.service';

@Component({
  selector: 'c8y-assign-devices',
  templateUrl: './assign-devices.component.html'
})
export class AssignDevicesComponent {
  @Input() currentGroupId: string;
  @Input() refresh = new EventEmitter<any>();
  @Output() onCancel = new EventEmitter<any>();
  @Output() onShowChildDevices = new EventEmitter<boolean>();
  @Output() selectedDevice = new EventEmitter<IManagedObject>();

  deviceQueryStringOutput: string;
  pendingStatus: boolean = false;
  pagination: Pagination = { pageSize: 20, currentPage: 1 };
  selected: string[] = [];
  baseQuery: any;
  canAssignDevice: boolean = false;
  actionControls: ActionControl[] = [];
  readonly isSelectable = true;
  readonly itemsSelectLimit = 15;
  private queriesUtil: QueriesUtil;

  constructor(private assignDevicesService: AssignDevicesService, private alert: AlertService) {
    this.queriesUtil = new QueriesUtil();
  }

  @HostListener('document:keydown.enter', ['$event']) onEnterKeyDown(event: KeyboardEvent) {
    if (this.selected.length > 0) {
      this.assignDevices();
    }
  }

  @HostListener('document:keydown.escape', ['$event']) onEscapeKeyDown(event: KeyboardEvent) {
    this.onCancel.emit();
  }

  ngOnInit() {
    this.setNotIncludedInGroupQuery();
  }

  setNotIncludedInGroupQuery() {
    const notIncludedInGroupQuery = { __not: { __bygroupid: this.currentGroupId } };
    this.baseQuery = notIncludedInGroupQuery;
  }

  setActionControls(showChildren: boolean) {
    const actionControls: ActionControl[] = [];
    const selectChildrenAction: ActionControl = {
      type: 'SELECT_CHILDREN',
      icon: 'enter-bottom',
      text: gettext('Select children'),
      callback: (asset: Row) => this.selectChildren(asset as IManagedObject),
      showIf: (asset: Row) =>
        asset.childDevices.references.length > 0
    };

    if (showChildren) {
      actionControls.push(selectChildrenAction);
    }
    this.actionControls = actionControls;
    this.refresh.emit();
  }

  async assignDevices() {
    this.pendingStatus = true;
    await this.assignDevicesService.assignDevices(this.currentGroupId, this.selected);
    this.pendingStatus = false;
    this.selected = [];

    this.onCancel.emit();
    this.refresh.emit();

    this.alert.success(gettext('Devices assigned to the group.'));
  }

  onSelected(selectedDevicesIDs: string[]) {
    this.selected = selectedDevicesIDs;
  }

  selectChildren(asset: IManagedObject) {
    this.onShowChildDevices.emit(true);
    this.selectedDevice.emit(asset);
  }
}
